.clear {
	width: 0px;
	display: block;
	height: 0px;
	visibility: hidden;
	clear: both;
	overflow: hidden
}
.clearfix:after {
	line-height: 0;
	width: 0px;
	display: block;
	height: 0px;
	visibility: hidden;
	clear: both;
	font-size: 0px;
	content: ' '
}
.clearfix {
	display: inline-block
}
* html .clearfix {
	height: 1%
}
.clearfix {
	display: block
}